button {
  padding: 17px 40px;
  border-radius: 10px;
  border: 0;
  background-color: white;
  box-shadow: rgb(0 0 0 / 5%) 0 0 8px;
  letter-spacing: 1.5px;
  text-transform: uppercase;
  font-size: 15px;
  transition: all 0.5s ease;
}

button:hover {
  letter-spacing: 3px;
  background-color: hsl(260, 77%, 10%);
  color: hsl(0, 0%, 100%);
  box-shadow: rgb(33, 15, 67) 0px 7px 29px 0px;
  /* box-shadow: 0 7px 29px 0 rgba(93, 24, 220, 0.5); */
}

button:active {
  letter-spacing: 3px;
  background-color: hsl(260, 9%, 87%);
  color: hsl(0, 0%, 100%);
  box-shadow: rgb(93 24 220) 0px 0px 0px 0px;
  transform: translateY(10px);
  transition: 100ms;
}

@media screen and (max-width: 448px) {
  button {
    padding: 15px 40px;
    letter-spacing: 1.5px;
    font-size: 13px;
  }
  button:active {
    letter-spacing: 1px;
    transform: translateY(0px);
  }
}

.inputbar {
  padding: 5% 1% 5% 1%;
  border-radius: 10px;
  border: none;
  color: rgb(10, 10, 10);
  background-color: rgb(243, 238, 238);
  width: 95%;
  margin: 0% 3% 4% 3%;
  transition: border 0.3s;
}
.inputbar:hover {
  border: #e87700 solid 0.5px;
}

